























































































import mediaList from '@/components/tab-nvue/mediaList.vue';
import headerSearch from '@/components/common/header-search.vue';
import indexCon from '@/components/index-content/indexContent.vue';
export default {
	components: {
		mediaList,
		headerSearch,
		indexCon
	},
	data() {
		return {
			headerPosition: "fixed",
			//轮播
			swiperList: [{
					id: 459,
					src: '自定义src0',
					image: 'http://static.lamamuying.com/static/images/banner/banner5.jpg'
				},
				{
					id: 151,
					src: '自定义src1',
					image: 'http://static.lamamuying.com/static/images/banner/banner6.jpg'
				},
				{
					id: 205,
					src: '自定义src2',
					image: 'http://static.lamamuying.com/static/images/banner/banner7.jpg'
				},
				{
					id: 67,
					src: '自定义src3',
					image: 'http://static.lamamuying.com/static/images/banner/banner8.jpg'
				}
			],
			notice: [{
					id: 2,
					title: '生完娃的辣妈们，应该这样减脂！'
				},
				{
					id: 3,
					title: '过了35岁这个极限年龄生孩子，孕妇要做好4件事'
				},
				{
					id: 4,
					title: '1岁内宝宝禁用的感冒药'
				},
			],
			videoList: [], // 热门视频
			hotList: [], // 热门文章
			scrollLeft: 0,
			newsList: [], //文章列表
			tabIndex: 0,
			tabBars: [] //文章tabbars
		}
	},
	// 视频
	onReady: function(res) {

		this.videoContext = uni.createVideoContext('myVideo')

	},
	onLoad: function() {
		uni.showLoading({
			title: '加载中',
		});
		/* 初始化列表信息 */
		this.getTabBars();
		this.getList();
		this.getHotList();
		this.getvideoList();
	},
	methods: {
		scanCode() {
			uni.scanCode({
				success: res => {
					//console.log('条码类型：' + res.scanType);
					//console.log('条码内容：' + res.result);						
					uni.request({
						url: this.apiUrl + 'user/getUser',
						method: 'GET',
						data: {
							uid: res.result
						},
						success: result => {
							//console.log(result);
							uni.setStorageSync('referee', result.data.data.id);
							uni.setStorageSync('locationShop', result.data.data.shop);
							this.goodsList = [];
							this.recGoods = [];
							this.page = 1;
							this.getGoodsList();
							this.getRecGoodsData();
						},
						fail: () => {},
						complete: () => {}
					});
					uni.showToast({
						title: '扫描成功'
					});
					uni.switchTab({
						url: '/pages/index/index'
					});
				}
			});
		},
		// 视频
		getvideoList() {
			uni.request({
				url: this.apiUrl + 'article/getMedias',
				method: 'GET',
				data: {
					// limit:4,
					is_top: 1,
				},
				success: res => {
					// console.log(res);
					if (res.data.code == 0) {
						this.videoList = res.data.data;
						// console.log(this.videoList);
					}
				},
				fail: () => {},
				complete: () => {}
			});

		},


		// 热门文章
		getHotList() {
			uni.request({
				url: this.apiUrl + 'article/getArticles',
				method: 'GET',
				data: {
					limit: 30
				},
				success: (result) => {
					if (result.data.code == 0) {
						this.hotList = result.data.data;
					}
				},
			});
		},
		// 调取tabbar选项卡的数据
		getTabBars() {
			uni.request({
				url: this.apiUrl + 'article/getCategory',
				method: 'GET',
				data: {
					pid: 1,
					child: 1,
					limit: 12
				},
				success: result => {
					//console.log(result.data.data);
					if (result.data.code == 0) {
						this.tabBars = result.data.data;
					}
				}
			});

		},
		//调取文章列表
		getList() {
			uni.request({
				url: this.apiUrl + 'article/getArticles',
				// data:activeTab.requestParams,
				method: 'GET',
				data: {
					cid: 2,
				},
				success: (result) => {
					if (result.data.code == 0) {
						this.newsList = result
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: function() {
					uni.hideLoading();
				}

			});
		},

		async changeTab(event) {
			/* 左右滑动切换数据 */
			if (event.detail.current === this.tabIndex) {
				return false;
			} else {
				this.tabIndex = event.detail.current;
			}

			let index = event.detail.current;
			if (this.isClickChange) {
				this.tabIndex = index;
				this.isClickChange = false;
				return;
			}
			let tabBar = await this.getElSize('tab-bar');
		},
		getNodeSize(node) {
			return new Promise((resolve, reject) => {
				dom.getComponentRect(node, (result) => {
					resolve(result.size);
				});
			});
		},
		onRefresh(event) {
			this.getList();
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select('#' + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async tapTab(index) { //点击tab-bar
			if (this.tabIndex === index) {
				return false;
			} else {
				let tabBar = await this.getElSize('tab-bar'),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = index;


			}
		},
	}

}
